
import { NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


const routes : Routes = [
    {
        path : 'manageTariff',
		loadChildren : () => import('./manage-tariff/manage-tariff.module').then(m=>m.ManageTariffModule),
		data : {title : 'Manage Tariff'}
    },
    {
        path : 'taxSetting',
		loadChildren : () => import('./tax-setting/tax-setting.module').then(m=>m.TaxSettingModule),
		data : {title : 'Tax Setting'}
    },
    {
        path : 'launchTariff',
		loadChildren : () => import('./launch-tariff/launch-tariff.module').then(m=>m.LaunchTariffModule),
		data : {title : 'Tax Setting'}
    },
]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class TariffManagementRoutingModule{}